<template>
  <div class="box1">
      <div class="title">
          <span class="one" @click="changeRouter">&lt;</span>
          <span class="two">会员注册</span>
      </div>
      <ul>
          <li>
              <div class="qiang"></div>
              <span>手机号</span>
              <input ref="phoneNumber" type="text" placeholder="请输入手机号">
              <div class="qiang"></div>
          </li>
          <li>
              <span>设置密码</span>
              <input type="password" ref="mima" placeholder="请输入新密码">
              <div class="qiang"></div>
          </li>
          <li>
              <span>确认密码</span>
              <input type="password" ref="remima" placeholder="请再次输入密码">
              <div class="qiang"></div>
          </li>
      </ul>
      <input class="checkbox" id="xieyi" type="checkbox" ref="tongyi">
      <label for="xieyi">
          <span class="xieyi">阅读并同意《注册协议》</span>
      </label>
      <button @click="zhuce">
          提交注册
      </button>
      <p class="tixing">登录账户为你输入的手机号</p>
  </div>
</template>

<script>
export default {
    name:'reg',
    data(){
        return{
            user:{userName:"",userMima:""}
        }
    },
    methods:{
        changeRouter(){
            this.$router.replace("/login");
        },
        zhuce(){
            if(this.$refs.phoneNumber.value==""){
                alert("请输入手机号");
                return;
            }
            if(this.$refs.mima.value==""){
                alert("请输入密码");
                return;
            }
            if(this.$refs.remima.value==""){
                alert("请重复输入密码");
                return;
            }
            let reg =/^0?1[3|4|5|6|7|8][0-9]\d{8}$/;
            // console.log(reg.test(this.$refs.phoneNumber.value));
            if(reg.test(this.$refs.phoneNumber.value)==false){
                alert("请输入正确格式的手机号");
                return;
            }
            if(this.$refs.mima.value!==this.$refs.remima.value){
                alert("两次密码不一致");
                return;
            }
            if(this.$refs.tongyi.checked==false){
                alert("请阅读并同意《注册协议》");
                return;
            }
            this.user.userName=this.$refs.phoneNumber.value;
            this.user.userMima=this.$refs.mima.value;
            localStorage.setItem(this.user.userName,JSON.stringify(this.user));
            alert("注册成功，按确定跳转到登录页面！");
            this.$router.replace("/login");
            //let that=this;
            // this.$http.post("http://www.dongxx.com/user",this.user)
            // .then(function(data){
            //     alert("注册成功，按确定跳转到登录页面！");
            //     that.$router.replace("/login");
            // })
            // .catch(function(err){
            //     console.log(err);
            // })
        //    console.log(this.$refs.tongyi.checked); 
        }
    }
}
</script>

<style scoped>
.box1{
    height: 50rem;
    background-color: white;
    position: relative;
    z-index: 999;
}
.box1 .qiang{
    width: 100%;
    height: 1px;
    background-color: #bbbbbb;
}
.box1 .title{
    position: relative;
    width: 100%;
    height: 44px;
    background-color: rgb(244,244,244);
}
.box1 .title .one{
    position: absolute;
    left: 5%;
    top: 50%;
    transform: translate(0,-50%);
    color: dimgray;
    font-size: 25px;
} 
.box1 .title .two{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    color: dimgray;
}
.box1 ul{
    list-style: none;
    padding-left: 0;
}
.box1 ul li span{
    padding-left: 10px;
}
.box1 ul li input{
    display: inline-block;
    width: 200px;
    border: 0px;
    margin-bottom: 0px;
}
.box1 button{
    position: absolute;
    left: 50%;
    top: 32%;
    transform: translate(-50%,0);
    width: 90%;
    height: 37px;
    border-radius: 5px;
    background-color: red;
    color: white;
}
.box1 .checkbox{
    margin-left: 10px;
}
.box1 .xieyi{
    font-size: 14px;
    color: gray;
}
.box1 .tixing{
    position: absolute;
    left: 50%;
    top: 38%;
    transform: translate(-50%,0);
}
</style>


