<template>
  <div class="box">
    <div class="yonghu">
      <div class="userImg">
        <img src="../../../static/photo/userImg.jpg" alt="">
      </div>
      <div class="text">
        <p class="one">暂无微信用户信息</p>
        <p class="two">等级：普通会员</p>
        <p class="three">手机：{{userId}}</p>
      </div>
    </div>
    <div class="dingdan">
      <div class="top">
        <img src="../../../static/userPhoto/dingdan.png" alt="">
        <span class="one">我的订单</span>
        <span class="two" @click="quanbu">全部订单</span>
      </div>
      <div class="bottom">
        <div @click="fukuan" class="one1">
          <img src="../../../static/userPhoto/fukuan.png" alt="">
          <p class="yuanquan">{{fukuanNumber}}</p>
          <p>待付款</p>
        </div>
        <div @click="fahuo" class="two1">
          <img src="../../../static/userPhoto/fahuo.png" alt="">
          <p class="yuanquan">{{fahuoNumber}}</p>
          <p>代发货</p>
        </div>
        <div @click="shouhuo" class="three1">
          <img src="../../../static/userPhoto/shouhuo.png" alt="">
          <p class="yuanquan">0</p>
          <p>待收货</p>
        </div>
        <div @click="pingjia" class="four1">
          <img src="../../../static/userPhoto/pingjia.png" alt="">
          <p class="yuanquan">0</p>
          <p>待评价</p>
        </div>
      </div>
    </div>
    <div class="qiang">
    </div>
    <router-link tag="div" to="/user/shoucang" class="tab">
      <img src="../../../static/userPhoto/shoucang.png" alt="">
      <span>我的收藏</span>
      <span class="jinru">&gt;</span>
    </router-link>
    <router-link tag="div" to="/user/dizhi" class="tab">
      <img src="../../../static/userPhoto/dizhi.png" alt="">
      <span>地址管理</span>
      <span class="jinru">&gt;</span>
    </router-link>
    <router-link tag="div" to="/user/xinxi" class="tab">
      <img src="../../../static/userPhoto/yonghu.png" alt="">
      <span>用户信息</span>
      <span class="jinru">&gt;</span>
    </router-link>
    <router-link to="/user/remima" tag="div" class="tab">
      <img src="../../../static/userPhoto/mima.png" alt="">
      <span>密码修改</span>
      <span class="jinru">&gt;</span>
    </router-link>
    <div @click="onExit" class="tab">
      <img src="../../../static/userPhoto/tuichu.png" alt="">
      <span>退出登录</span>
      <span class="jinru">&gt;</span>
    </div>
    <homeFooter></homeFooter>
  </div>
</template>

<script>
import homeFooter from '../home/components/homeFooter.vue'
export default {
    name:'user',
    data(){
      return{
        userId:"null",
        fahuoNumber:0,
        fukuanNumber:0
      }
    },
    components:{
      homeFooter
    },
    methods:{
      quanbu(){
        this.$router.replace({path:'/dingdan',query:{id:'quanbu'}});
      },
      fukuan(){
        this.$router.replace({path:'/dingdan',query:{id:'fukuan'}});
      },
      fahuo(){
        this.$router.replace({path:'/dingdan',query:{id:'fahuo'}});
      },
      shouhuo(){
        this.$router.replace({path:'/dingdan',query:{id:'shouhuo'}});
      },
      pingjia(){
        this.$router.replace({path:'/dingdan',query:{id:'pingjia'}});
      },
      onExit(){
        let a=confirm("确定要退出当前账户吗？");
        if(a==false){
          return;
        }else{
          this.$router.replace('/login');
          localStorage.removeItem('user');
        }
      }
    }
    ,
    created(){
      this.userId=this.$route.query.id;
      let user=localStorage.getItem('user');
      let userData=JSON.parse(localStorage.getItem(user));
      if(userData.userFukuan!=undefined){
        this.fukuanNumber=userData.userFukuan.length;
        this.fahuoNumber=userData.userShouhuo.length;
      }
    }
}
</script>

<style scoped>
.box .yonghu{
  position: relative;
  width: 100%;
  height: 100px;
  background-color: orangered;
  padding: 5px;
}
.box .yonghu .userImg img{
  height: 70px;
  width: 70px;
  border-radius: 50%;
}
.box .yonghu .userImg{
  position: absolute;
  top: 50%;
  left: 5%;
  transform: translate(0,-50%);
  
}
.box .yonghu .text{
  position: absolute;
  left: 30%;
  top: 50%;
  transform: translate(0,-50%);
  color: white;
  padding-top: 8px;
}
.box .yonghu .text .one{
    margin-bottom: 7px;
    color: white;
}
.box .yonghu .text .two{
  margin-bottom: 7px;
  color: white;
  font-size: 90%;
}
.box .yonghu .text .three{
  margin-bottom: 7px;
  color: white;
}
.dingdan{
  width: 100%;
}
.dingdan .top{
  position: relative;
  padding: 10px;
  height: 42px;
  width: 100%;
}
.dingdan .top img{
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  transform: translate(0,-50%);
  left: 3%;
}
.dingdan .top .one{
  position: absolute;
  top: 50%;
  transform: translate(0,-50%);
  left: 8%;
  font-size: 90%;
}
.dingdan .top .two{
  float: right;
  font-size: 90%;
}
.dingdan .bottom{
  padding: 10px;
  width: 100%;
  height: 68px;
}
.dingdan .bottom .one1,.two1,.three1,.four1{
  padding-top: 30px;
  position: relative;
  float: left;
  width: 25%;
}
.dingdan .bottom img{
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%,0);
  width: 30px;
  height: 30px;
} 
.dingdan .bottom .yuanquan{

  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: red;
  color: white;
  top: 0px;
  right: 25%;
  font-size: 12px;
  line-height: 14px;
  align-items: center;
}

.dingdan .bottom p{
  text-align: center;
}
.qiang{
  width: 100%;
  height: 10px;
  background-color: #f2f2f2;
}
.tab{
  position: relative;
  height: 42px;
  padding: 10px;
}
.tab img{
position: absolute;
top: 50%;
left: 2%;
transform: translate(0,-50%);
  width: 22px;
  height: 22px;
  margin-right: 10px;
}
.tab span:nth-of-type(1){
position: absolute;
top: 50%;
left: 10%;
transform: translate(0,-50%);
}
.tab .jinru{
float: right;
color: gray;
}
</style>


