<template>
  <div>
    <div class="dingbu">
          <span @click="onChangeRouter">&lt;</span>
          <p>下单页面</p>
      </div>
      <div class="dizhiAll">
        <div v-for="(item,index) of JSON.parse(JSON.stringify(dizhi))" :key="index" class="dizhi">
        <label for="">
          <input type="radio" name="dizhi" :checked="item.checkedIf">{{item.user}}--{{item.phone}}
          <p>{{item.province}}{{item.city}}{{item.town}}{{item.xiangxi}}</p>
        </label>
      </div>
        <button class="add" @click="addDizhi">新增</button>
      </div>
      <div class="qiang"></div>
      <div  v-for="(item,index) of JSON.parse(JSON.stringify(goods))"  :key="index" class="kuai">
      <div class="zuobian">
          <img class="goodPhoto" :src="item.photoUrl" alt="">  
      </div>
      <div class="youbian">
        <p>{{item.name}}</p>
        <span class="shuoming">{{item.kuaidi}} | {{item.fromPlace}}</span>
        <div class="last">
          <span>￥{{item.price}}</span>
          <span>&times;{{item.number}}</span>
        </div>
      </div>
      <div class="xian"></div>
    </div>
    <div class="qiang"></div>
    <div class="dibu">
      <div class="one">
        <span>配送方式</span>
        <span>快递</span>
      </div>
      <div class="two">
        <span>运费</span>
        <span>￥0.00</span>
      </div>
      <div class="three">
        <span>留言</span>
        <input type="text" placeholder="请输入留言内容">
      </div>
    </div>
    <div class="qiang"></div>
    <p class="price">总费用：<span>￥{{this.price}}</span></p>
    <div class="button1">
      <button @click="xiadan">下单支付</button>
    </div>
    <div class="gg"></div>
  </div>
</template>

<script>
export default {
    name:'goumai',
    data(){
      return{
        dizhi:[],
        goods:[],
        price:0
      }
    },
    methods:{
      onChangeRouter(){
        this.$router.replace('/shopcar');
      },
      addDizhi(){
        this.$router.replace('/user/dizhiList');
      },
      xiadan(){
        let user=localStorage.getItem('user');
        let userData=JSON.parse(localStorage.getItem(user));
        //清除购物车商品
        for(let i=0;i<this.goods.length;i++){
          for(let j=0;j<userData.shopcar.length;j++){
            if(userData.shopcar[j].id==this.goods[i].id){
              for(let x=j;x<userData.shopcar.length-1;x++){
                userData.shopcar[x]=userData.shopcar[x+1];
              }
              userData.shopcar.pop();
            }
          }
        }
        // console.log(userData.shopcar);
        if(userData.userFukuan==undefined){
          userData.userFukuan=[];
        }
        if(userData.userShouhuo==undefined){
          userData.userShouhuo=[];
        }
        if(userData.userAll==undefined){
          userData.userAll=[];
        }
        let userFukuan={
          id:userData.userAll.length+1,
          data:[]
        }
        let userAll={
          id:userData.userAll.length+1,
          data:[],
          goumaiIf:false
        }
        for(let i=0;i<this.goods.length;i++){
          let userFukuan1={
          id:this.goods[i].id,
          price:this.goods[i].price,
          fromPlace:this.goods[i].fromPlace,
          photoUrl:this.goods[i].photoUrl,
          kuaidi:this.goods[i].kuaidi,
          number:this.goods[i].number,
          name:this.goods[i].name
        }
        userFukuan.data.push(userFukuan1);
        userAll.data.push(userFukuan1);
        // console.log(userAll)
        }
        userData.userFukuan.unshift(userFukuan);
        userData.userAll.unshift(userAll);
        // console.log(userFukuan);
        //初始化
        // userData.userAll=[];
        // userData.userShouhuo=[];
        // userData.userFukuan=[]
        localStorage.setItem(user,JSON.stringify(userData));
        this.$router.replace('/dingdan');
      }
    },
    mounted(){
        this.price=0;
        this.$store.commit('changeDisplay',0);
        let user=localStorage.getItem('user');
        let userData=JSON.parse(localStorage.getItem(user));
        this.dizhi=userData.userDizhi;
        for(let i=0;i<userData.shopcar.length;i++){
          if(userData.shopcar[i].check==true){
            this.price=userData.shopcar[i].price*userData.shopcar[i].number+this.price;
            this.goods.push(userData.shopcar[i]);
          }
        }
    }
}
</script>

<style scoped>
.dingbu{
    background-color: #f2f2f2;
    position: relative;
    height: 44px;
    width: 100%;
}
.dingbu span{
    line-height: 44px;
    font-size: 200%;
    margin-left: 10px;
    color: #8F8F94;
}
.dingbu p{
    line-height: 44px;
    font-size: 100%;
    display: inline-block;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.dizhiAll{
  padding: 10px;
  position: relative;
}
.dizhiAll .dizhi{
  font-size: 16px;
  color: black;
}
.dizhiAll .dizhi p{
  font-size: 16px;

}
.dizhiAll .add{
  position: absolute;
  top: 50%;
  right: 5%;
  transform: translateY(-50%);
  background-color: green;
  color: white;

}
.qiang{
  height: 10px;
  background-color: #f2f2f2;
}
.kuai .zuobian{
  display: inline-block;
  position: relative;
  width: 25%;
  height: 100px;
}

.kuai .zuobian .goodPhoto{
  display: inline-block;
  width: 82px;
  height: 82px;
  position: absolute;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid grey;
  box-shadow: 2px 2px 2px grey;
}
.kuai .youbian{
  margin-top: 10px;
  float: right;
  display: inline-block;
  position: relative;
  width: 73%;
}
.kuai .youbian p{
  display: block;
  height: 22px;
  line-height: 22px;
  overflow: hidden;
  color: black;
}
.kuai .youbian .shuoming{
  font-size: 13px;
  color: #8f8f94;
}
.kuai .youbian .last{
  margin-top: 8px;
}
.kuai .youbian .last span:nth-child(1){
  color: red;
  line-height: 20px;
}
.kuai .youbian .last span:nth-child(2){
  float: right;
  margin-right: 10px;
}
.kuai .xian{
  height: 1px;
  background-color: #f2f2f2;
}
.dibu .one,.two,.three{
  height: 41px;
  padding: 10px;
  border-bottom: 1px solid #f2f2f2;
}
.dibu .one span:nth-child(1){
  float: left;
}
.dibu .one span:nth-child(2){
  float: right;
  color: red;
}
.dibu .two span:nth-child(1){
  float: left;
}
.dibu .two span:nth-child(2){
  float: right;
  color: red;
}
.dibu .three span{
  float: left;
}
.dibu .three input{
  display: inline-block;
  width: 200px;
  padding: 0;
  margin: 0;
  border: 0;
  height: 18px;
  line-height: 18px;
  font-size: 14px;
  margin-left: 10px;
}
.gg{
  height: 80px;
}
.price{
  line-height: 50px;
  text-align: center;
  font-size: 18px;
  color: black;
}
.price span{
  color: red;
  font-weight: bold;
}
.button1{
  position: relative;
}
.button1 button{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
  height: 40px;
  background-color: green;
  color: white;
  border-radius: 5px;
}
</style>


