<template>
  <div>
      <div class="dingbu">
          <span @click="onChangeRouter">&lt;</span>
          <p>密码修改</p>
      </div>
      <div class="qiang"></div>
      <div class="gg">
          <p>用户名:</p>
          <input class="only" type="text" disabled :value="userId">
      </div>
      <div class="gg">
          <p>原密码:</p>
          <input ref="mima" type="password" placeholder="请输入新密码">
      </div>
      <div class="gg">
          <p>修改密码:</p>
          <input ref="remima" type="password" placeholder="请再次输入新密码">
      </div>
      <div class="queding">
          <button @click="onChangeMima">确认修改密码</button>
      </div>
  </div>
</template>

<script>
export default {
    name:'remima',
    data(){
        return{
            userId:""
        }
    },
    methods:{
        onChangeRouter(){
            this.$router.replace({path:'/user',query:{id:localStorage.getItem('user')}});
        },
        onChangeMima(){
            if(this.$refs.mima.value==""){
                alert("请输入原密码");
                return;
            }
            if(this.$refs.remima.value==""){
                alert("请输入新密码");
                return;
            }
            let userId=localStorage.getItem('user');
            let mima=JSON.parse(localStorage.getItem(userId));
            // console.log(mima.userMima);
            if(this.$refs.mima.value!=mima.userMima){
                alert("原密码填写错误，请重新输入");
                return;
            }
            if(this.$refs.remima.value==mima.userMima){
                alert("新密码不要和原密码重合");
                return ;
            }
            localStorage.setItem(userId,JSON.stringify({"userName":userId,"userMima":this.$refs.remima.value}));
            alert("密码修改成功");
        }
    },
    created(){
        this.userId=localStorage.getItem('user');
    }
}
</script>

<style scoped>
.dingbu{
    background-color: #f2f2f2;
    position: relative;
    height: 44px;
    width: 100%;
}
.dingbu span{
    line-height: 44px;
    font-size: 200%;
    margin-left: 10px;
    color: #8F8F94;
}
.dingbu p{
    line-height: 44px;
    font-size: 100%;
    display: inline-block;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.qiang{
    height: 20px;
    border-bottom: 1px solid #f2f2f2;
}
.gg{
    position: relative;
    width: 100%;
    height: 40px;
    border-bottom: 1px solid #bbbbbb;
}
.gg p{
    display: inline-block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: black;
    margin-left: 10px;
    font-size: 100%;
}
.gg input{
    color: black;
    font-size: 90%;
    position: absolute;
    left: 25%;
    width: 250px;
    height: 20px;
    border: 0;
    margin: 0;
    padding: 0;
    top: 50%;
    transform: translateY(-50%);
}
.gg .only{
    font-weight: bold;
}
.queding{
    position: relative;
    width: 100%;
    height: 60px;
    background-color: #f2f2f2;
}
.queding button{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 90%;
    height: 70%;
    background-color: red;
    color: white;
    border-radius: 5px;
}
</style>


