<template>
  <div>
      <nav class="mui-bar mui-bar-tab"  ref="muibar">
			<a ref="home" class="mui-tab-item mui-active" @click="changeRouter('/')">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label">首页</span>
			</a>
			<a ref="classify" class="mui-tab-item" @click="changeRouter('/classify')">
				<span class="mui-icon mui-icon-settings"></span>
				<span class="mui-tab-label">分类</span>
			</a>
			<a ref="shopcar" class="mui-tab-item" @click="changeRouter('shopcar')">
				<span class="mui-icon iconfont icon-gouwuchezhengpin"><span  class="mui-badge">{{goodNumber}}</span></span>
				<span class="mui-tab-label">购物车</span>
			</a>
			<a ref="user" class="mui-tab-item" @click="changeRouter('user')"> 
				<span class="mui-icon iconfont icon-yonghu"></span>
				<span class="mui-tab-label">用户</span>
			</a>
		</nav>
  </div>
</template>

<script>
export default {
	name:'homeFooter',
	data(){
		return{
			number:0
		}
	},
	computed:{
		goodNumber(){
			return this.$store.state.number;
		}
	},
    methods:{
        changeRouter(path){
			// console.log(this.$refs.home);
			var that=this;
			if(path==='user'){
				if(localStorage.getItem("user")==undefined){
					this.$refs.user.classList.remove('mui-active');
					let routerName=this.$route.name;
					eval("this.$refs."+routerName+".classList.add('mui-active')");
					let a=confirm("请先登录，点击确定跳转到登录页面");
						if(a===false){
							
						}else{
							that.$router.replace('/login');
						}
				}else{
					this.$router.replace({path:'/user',query:{id:localStorage.getItem("user")}});
				}
				return ;
			}
			if(path=='shopcar'){
				if(localStorage.getItem("user")==undefined){
					this.$refs.shopcar.classList.remove('mui-active');
					let routerName=this.$route.name;
					eval("this.$refs."+routerName+".classList.add('mui-active')");
					let a=confirm("还没有用户登录，请先登录!");
					if(a===false){
					}else{
						that.$router.replace('/login');
					}
					return;
				}
			}
			this.$router.replace(path);
		},
	},
	mounted(){
		// console.log(this.$store);
		let user=localStorage.getItem('user');
		if(user!=undefined){
			let userData=JSON.parse(localStorage.getItem(user));
			let number=userData.shopcar.length;
			//vuex
			this.$store.commit('changeNumber',number);
		}
		//vuex 管理底部栏的隐藏或显示
		let display=this.$store.state.display;
		// console.log(display);
		// if(display==0){
		// 	console.log(1);
		// 	this.$refs.muibar.style.display='none';
		// }
		let a = this.$route.name;
		// console.log(a);
		// console.log(this.$refs.a,a);
		this.$refs.home.classList.remove('mui-active')
		this.$refs.classify.classList.remove('mui-active');
		this.$refs.shopcar.classList.remove('mui-active');
		this.$refs.user.classList.remove('mui-active');
		if(a=='user'||a=='remima'||a=='xinxi'||a=='shoucang'||a=='dizhi'||a=='dizhiList'){
			this.$refs.user.classList.add('mui-active');
		}
		// this.$refs.home.classList.add('mui-active');
		if(a=='home'||a=='shopcar'||a=='classify'){
			eval("this.$refs."+a+".classList.add('mui-active')");
		}
	 }
	
}
</script>

<style>
    .mui-bar-tab .mui-tab-item.mui-active{
        color: #e02e24;
        font-weight: bold;

    }
</style>


