import vue from 'vue'
import vuex from 'vuex'
vue.use(vuex);
let user=localStorage.getItem('user');
// let userData=JSON.parse(localStorage.getItem(user));

const store=new vuex.Store({
    state:{
        number:user==undefined?0:JSON.parse(localStorage.getItem(user)).shopcar.length,
        display:1
    },
    mutations:{
        changeNumber(state,msg){
            state.number=msg;
        },
        changeDisplay(state,msg){
            state.display=msg;
        }
    }
})
export default store


