<template>
  <div>
		<swiper class="swiperImg" ref="mySwiper" :options="swiperOptions">
			<swiper-slide><img :src="swiperData[0].photoUrl" alt=""></swiper-slide>
			<swiper-slide><img :src="swiperData[1].photoUrl" alt=""></swiper-slide>
			<swiper-slide><img :src="swiperData[2].photoUrl" alt=""></swiper-slide>
			<swiper-slide><img :src="swiperData[3].photoUrl" alt=""></swiper-slide>
			<div class="swiper-pagination" slot="pagination"></div>
		</swiper>
		<div class="geban1">
		</div>
		<div class="wuzhounian" >
			<img :src="JSON.parse(JSON.stringify(wuzhounian))[0].photoUrl" alt="">
		</div>
		<div class="geban1">	
		</div>
			<router-link tag="div" :to="{path:'/home/goods01',query:{id:index}}" class="goods"  v-for="(item,index) in JSON.parse(JSON.stringify(goodsDetail))" :key="index" >  
				<div class="picture">
					<img :src="item.photoUrl" alt="">
				</div>
				<div class="text">
					<div class="goodDesc">
						{{item.text}}
					</div>
					<div class="goodPrice">
						<p class="p1">￥{{item.price}}</p>
						<p class="p2">{{item.people}}</p>
					</div>
					<div class="goodBottom">
						<div class="yunfei">{{item.data.kuaidi}}</div>
						<div class="diqu">{{item.data.fromPlace}}</div>
					</div>
				</div>
			</router-link>
			<div class="height"></div>
  </div>
</template>

<script>
import homeFooter from './homeFooter.vue'
var gallery = mui('.mui-slider');
gallery.slider({
  interval:2000//自动轮播周期，若为0则不自动播放，默认为0；
});
import mui from "../../../mui/js/mui.js"
export default {
    name:'shouye',
    data(){
      return{
		wuzhounian:[1],
		swiperOptions: {
			loop:true,
          pagination: {
            el: '.swiper-pagination'
		  },
		  autoplay:{
			delay:3000,
			stopOnLastSlide: false,

    		/* 触摸滑动后是否继续轮播 */

    		disableOnInteraction: false
		  },
		  speed:1000,
          // Some Swiper option/callback...
        },
		swiperData:[1,2,3,4],
		goodsDetail:[],
      }
	},
	components:{
		homeFooter
	},
    created(){
	  var that=this;
	  this.$http.post("http://www.dongxx.com/home/wuzhounian",{})
	  .then(function(data){
		// console.log(that.wuzhounian);
		that.wuzhounian=data["data"]["data"];
		// console.log(that.wuzhounian);
	  })
	  .catch(function(err){
		  console.log(err);
	  })
      this.$http.post('http://www.dongxx.com/home/homeSwiperData',{})
      .then(function(data){
        that.swiperData=data["data"]["data"];
        //console.log(typeof(data),that.swiperData[0]);
      })
      .catch(function(err){
        console.log(err);
	  });
	  this.$http.post("http://www.dongxx.com/home/goods",{})
	  .then(function(data){
		  that.goodsDetail=data["data"]["data"];
	  })
	  .catch(function(err){
		  console.log(err);
	  })
	}
}
</script>

<style>
.swiper-pagination-bullet{
	background-color: rgb(255, 255, 255);
	opacity: 0.3;
}
.swiper-pagination-bullet-active{
	background-color: rgb(255, 80, 0);
	opacity: 1;
}
.swiperImg img{
	width: 100%;
	height: 100%;
}
.wuzhounian img{
	width: 100%;
	height: 100%;
}
.geban1{
	background-color: rgb(244,244,244);
	width: 100%;
	height: 0.5rem;
}
.goods{
	position: relative;
	width: 24rem;
	height: 11rem;
	margin-top: 0.5rem;
	margin-left: 0.2rem;
	margin-right: 0.2rem;
}
.goods .picture img{
	width: 11rem;
	height: 11rem;
	
}
.goods .goodDesc{
	margin-top: 0.2rem;
	position: absolute;
	left: 50%;
	top: 0;
	width: 11rem;
	height: 3rem;
	overflow: hidden;
	line-height: 1.5rem;
	font-size: 0.9rem;
}
.goods .goodPrice{
	position: absolute;
	left: 50%;
	top: 60%;
	height: 1.5rem;
	
}
.goods .goodPrice .p1{
	display: inline-block;
	color: red;
	font-size: 1.4rem;
}
.goods .goodPrice .p2{
	display: inline-block;
	font-size: 0.6rem;
}
.goods .goodBottom{
	position: absolute;
	left: 50%;
	top: 85%;
	width: 11rem;
	height: 0.4rem;
}
.goods .goodBottom .yunfei{
	float: left;
	font-size: 0.5rem;
}
.goods .goodBottom .diqu{
	float: right;
	font-size: 0.5rem;
}
.height{
	width: 100%;
	height: 4rem;
}
</style>


