<template>
  <div>
      <homeHeader @change="changeVue"></homeHeader>
      <shouye v-if="tabChange[0]"></shouye>
      <nvzhuang v-if="tabChange[1]"></nvzhuang>
      <xiebao v-if="tabChange[2]"></xiebao>
      <baihuo v-if="tabChange[3]"></baihuo>
      <shiping v-if="tabChange[4]"></shiping>
      <shouji v-if="tabChange[5]"></shouji>
        <homeFooter></homeFooter>
  </div>
</template>

<script>
import homeHeader from "./components/homeHeader"
import shouye from "./components/shouye"
import nvzhuang from './components/nvzhuang'
import xiebao from './components/xiebao'
import baihuo from './components/baihuo'
import shiping from './components/shiping'
import shouji from './components/shouji'
import homeFooter from './components/homeFooter'
export default {
    name:'home',
    data(){
        return{
            tabChange:[true,false,false,false,false,false]
        }
    },
    components:{
        homeHeader,shouye,nvzhuang,xiebao,baihuo,shiping,shouji,homeFooter
    },
    methods:{
        changeVue(data){
            switch(data){
                case 1: this.tabChange=[true,false,false,false,false,false]; break;
                case 2: this.tabChange=[false,true,false,false,false,false]; break;
                case 3: this.tabChange=[false,false,true,false,false,false]; break;
                case 4: this.tabChange=[false,false,false,true,false,false]; break;
                case 5: this.tabChange=[false,false,false,false,true,false]; break;
                case 6: this.tabChange=[false,false,false,false,false,true]; break;
                default: break;
            }
        }
    }
}
</script>

<style>

</style>


