<template>
  <div>
      <div class="dingbu">
          <span @click="onChangeRouter">&lt;</span>
          <p>用户信息修改</p>
      </div>
      <div class="qiang"></div>
      <div class="gg">
          <p>用户名:</p>
          <input class="only" type="text" disabled :value="userId">
      </div>
      <div  class="gg">
          <p>用户姓名</p>
          <input ref="name" type="text" placeholder="请输入用户姓名">
      </div>
      <div  class="gg">
          <p>用户性别</p>
          <select ref="sex" name="" id="">
              <option value="1" selected>男</option>
              <option value="2">女</option>
          </select>
      </div>
      <div class="gg">
          <p>用户QQ</p>
          <input  ref="qq" type="text" placeholder="请再次输入用户QQ">
      </div>
      <div class="gg">
          <p>用户邮箱</p>
          <input  ref="email" type="text" placeholder="请输入用户邮箱">
      </div>
      <div class="queding">
          <button @click="onChangeXinxi">确认修改用户信息</button>
      </div>
  </div>
</template>

<script>
export default {
    name:'xinxi',
    data(){
        return{
            userId:""
        }
    },
    methods:{
        onChangeRouter(){
            this.$router.replace({path:'/user',query:{id:localStorage.getItem('user')}});
        },
        onChangeXinxi(){
            // var obj = document.getElementByIdx_x(”testSelect”); //定位id
            // var index = obj.selectedIndex; // 选中索引
            // var text = obj.options[index].text; // 选中文本
            // var value = obj.options[index].value; // 选中值
            let i=this.$refs.sex.selectedIndex;
            let user=localStorage.getItem('user');
            let name=this.$refs.name.value;
            let sex=this.$refs.sex.options[i].text;
            let qq=this.$refs.qq.value;
            let email=this.$refs.email.value;
            let userData=JSON.parse(localStorage.getItem(user));
            // console.log(this.$refs.sex.options[i].text);
            let xinxi={
                'name':name,
                'sex':sex,
                'qq':qq,
                'email':email
            };
            userData.userXinxi=xinxi;
            localStorage.setItem(user,JSON.stringify(userData));
            alert('用户信息修改成功');
        }
    },
    mounted(){
        this.userId=localStorage.getItem('user');
        let user=localStorage.getItem('user');
        // console.log(user);
        let userData=JSON.parse(localStorage.getItem(user));
        // console.log(userData);
        if(userData.userXinxi==undefined){
        }else{
            this.$refs.name.value=userData.userXinxi.name;
            this.$refs.qq.value=userData.userXinxi.qq;
            this.$refs.email.value=userData.userXinxi.email;
            if(userData.userXinxi.sex=='女'){
                this.$refs.sex.options[0].selected='false';
                this.$refs.sex.options[1].selected='true';
            }
        }
    }
}
</script>

<style scoped>
.dingbu{
    background-color: #f2f2f2;
    position: relative;
    height: 44px;
    width: 100%;
}
.dingbu span{
    line-height: 44px;
    font-size: 200%;
    margin-left: 10px;
    color: #8F8F94;
}
.dingbu p{
    line-height: 44px;
    font-size: 100%;
    display: inline-block;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.qiang{
    height: 20px;
    border-bottom: 1px solid #f2f2f2;
}
.gg{
    position: relative;
    width: 100%;
    height: 40px;
    border-bottom: 1px solid #bbbbbb;
}
.gg p{
    display: inline-block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: black;
    margin-left: 10px;
    font-size: 100%;
}
.gg input{
    color: black;
    font-size: 90%;
    position: absolute;
    left: 25%;
    width: 250px;
    height: 20px;
    border: 0;
    margin: 0;
    padding: 0;
    top: 50%;
    transform: translateY(-50%);
}
.gg .only{
    font-weight: bold;
}
.queding{
    position: relative;
    width: 100%;
    height: 60px;
    background-color: #f2f2f2;
}
.queding button{
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 90%;
    height: 70%;
    background-color: red;
    color: white;
    border-radius: 5px;
}
.gg select{
    color: black;
    font-size: 90%;
    position: absolute;
    left: 25%;
    width: 50px;
    height: 20px;
    border: 0;
    margin: 0;
    padding: 0;
    top: 50%;
    transform: translateY(-50%);
    background: url('../../../static/userPhoto/select.png') no-repeat right center;
    background-size: 12px;
}
</style>


