<template>
  <div class="box">
    <div class="dingbu">
        <span @click="onChangeRouter">&lt;</span>
        <p>地址列表</p>
    </div>
    <div v-for="(item,index) of dizhi" :key="index" class="dizhi">
        <div class="dizhi2">
            <p class="one">{{item.user}}--{{item.phone}}</p>
            <p class="two">{{item.province}}{{item.city}}{{item.town}}{{item.xiangxi}}</p>
            <p v-if="item.checkedIf" class="three">默认地址</p>
            <img class="rename" @click="renameDizhi(index)" src="../../../static/userPhoto/rename.png" alt="">
            <img class="delete" @click="deleteDizhi(index)" src="../../../static/userPhoto/delete.png" alt="">
        </div>
        <div class="qiang"></div>
    </div>
    <button @click="tianjia" class="tianjia">
        添加新地址
    </button>
  </div>
</template>

<script>
export default {
    name:'dizhi',
    data(){
        return{
            dizhi:[]
        }
    },
    methods:{
        onChangeRouter(){
            this.$router.replace({path:'/user',query:{id:localStorage.getItem('user')}});
        },
        tianjia(){
            this.$router.replace('/user/dizhiList');
        },
        renameDizhi(index){
            this.$router.replace({path:'/user/dizhiList',query:{id:index}});
        },
        deleteDizhi(index){
            let j = confirm("你确定要删除该项地址信息吗？");
            if(j==false){
                return;
            }
            let user=localStorage.getItem('user');
            let userData=JSON.parse(localStorage.getItem(user));
            for(let i=index;i<userData.userDizhi.length-1;i++){
                userData.userDizhi[i]=userData.userDizhi[i+1];
            }
            userData.userDizhi.pop();
            this.dizhi=userData.userDizhi;
            localStorage.setItem(user,JSON.stringify(userData));
            alert("删除成功");
        }
    },
    mounted(){
        let user=localStorage.getItem('user');
        let userData=JSON.parse(localStorage.getItem(user));
        this.dizhi=userData.userDizhi;
    }
}
</script>

<style scoped>
.dingbu{
    background-color: #f2f2f2;
    position: relative;
    height: 44px;
    width: 100%;
}
.dingbu span{
    line-height: 44px;
    font-size: 200%;
    margin-left: 10px;
    color: #8F8F94;
}
.dingbu p{
    line-height: 44px;
    font-size: 100%;
    display: inline-block;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.box .tianjia{
    margin-top: 20px;
    position: relative;
    width: 90%;
    height: 40px;
    background-color: green;
    color: white;
    border-radius: 5px;
    left: 50%;
    transform: translateX(-50%);
}
.dizhi .qiang{
    background-color: #f2f2f2 ;
    height: 10px;
}
.dizhi2{
    padding: 10px;
    position: relative;
}
.dizhi .one{
    color: black;
    font-size: 17px;
    margin-bottom: 0;
}
.dizhi .two{
    font-size: 80%;
    margin-top: 8px;
    margin-bottom: 0;
}
.dizhi .three{
    display: inline-block;
    margin-top: 8px;
    background-color: orange;
    color: white;
    margin-bottom: 0;
    width: 80px;
    height: 26px;
    line-height: 26px;
    text-align: center;
    vertical-align: center;
}
.dizhi2 .rename{
    width: 25px;
    height: 25px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 20%;
}
.dizhi2 .delete{
    width: 25px;
    height: 25px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 8%;
}
</style>


