<template>
  <div class="homeHeader">
      <div class="mui-content">
			<div id="slider" class="mui-slider">
				<div id="sliderSegmentedControl"  class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
					<div class="mui-scroll">
						<a class="mui-control-item mui-active" @click="changeHomeTab(1)" href="#item1mobile" data-wid="tab-top-subpage-1.html">
							推荐
						</a>
						<a class="mui-control-item" href="#item2mobile" @click="changeHomeTab(2)" data-wid="tab-top-subpage-2.html">
							女装
						</a>
						<a class="mui-control-item" href="#item3mobile"  @click="changeHomeTab(3)" data-wid="tab-top-subpage-3.html">
							鞋包
						</a>
						<a class="mui-control-item" href="#item4mobile" @click="changeHomeTab(4)" data-wid="tab-top-subpage-4.html">
							百货
						</a>
						<a class="mui-control-item" href="#item5mobile" @click="changeHomeTab(5)" data-wid="tab-top-subpage-5.html">
							食品
						</a>
                        <a class="mui-control-item" href="#item5mobile" @click="changeHomeTab(6)" data-wid="tab-top-subpage-5.html">
							手机
						</a>
					</div>
				</div>

			</div>
		</div>
  </div>
</template>

<script>
import mui from '../../../mui/js/mui.js'
mui('.mui-scroll-wrapper').scroll({
	deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
});
export default {
	name:'homeHeader',
	methods:{
		changeHomeTab(data){
			this.$emit('change',data);
		}
	}
}
</script>

<style>
*{
    touch-action: pan-y;
}
.mui-segmented-control.mui-segmented-control-inverted .mui-control-item.mui-active{
    color: #e02e24;
    font-weight: bold;
}
.mui-slider{
    background-color: white;
}
</style>


