<template>
  <div class="box">
    <div class="dingbu">
        <span @click="onChangeRouter">&lt;</span>
        <p>收藏</p>
    </div>
    <div  v-for="(item,index) of shoucangData" @click="returnGoods(item)"  :key="index" class="kuai">
      <div class="zuobian">
          <img class="goodPhoto" :src="item.photoUrl" alt="">  
      </div>
      <div class="youbian">
        <p>{{item.name}}</p>
        <span class="shuoming">{{item.kuaidi}} | {{item.fromPlace}}</span>
        <img @click.stop="deleteAll(index)" class="delete" src="../../../static/userPhoto/delete.png" alt="">
        <div class="last">
          <span>￥{{item.price}}</span>
        </div>
      </div>
      <div class="qiang"></div>
    </div>
  </div>
</template>

<script>
export default {
    name:'shoucang',
    data(){
        return{
            shoucangData:[]
        }
    },
    methods:{
        returnGoods(item){
            this.$router.replace({path:'/home/goods01',query:{id:item.id-1}})
        }
        ,
        onChangeRouter(){
            this.$router.replace({path:'/user',query:{id:localStorage.getItem('user')}});
        },
        deleteAll(index){
            var a=confirm("你真的要删除该收藏吗？");
            if(a=false){
                return;
            }
            let user=localStorage.getItem('user');
            let userData=JSON.parse(localStorage.getItem(user));
            let shoucangData=userData.shoucang;
            for(let i=index;i<shoucangData.length-1;i++){
                shoucangData[i]=shoucangData[i+1];
            }
            shoucangData.pop();
            userData.shoucang=shoucangData;
            this.shoucangData=shoucangData;
            localStorage.setItem(user,JSON.stringify(userData));
        }
    },
    created(){
        let user=localStorage.getItem('user');
        let userData=JSON.parse(localStorage.getItem(user));
        this.shoucangData=userData.shoucang;
    }
}
</script>

<style scoped>
.dingbu{
    background-color: #f2f2f2;
    position: relative;
    height: 44px;
    width: 100%;
}
.dingbu span{
    line-height: 44px;
    font-size: 200%;
    margin-left: 10px;
    color: #8F8F94;
}
.dingbu p{
    line-height: 44px;
    font-size: 100%;
    display: inline-block;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.kuai .zuobian{
  display: inline-block;
  position: relative;
  width: 25%;
  height: 100px;
}

.kuai .zuobian .goodPhoto{
  display: inline-block;
  width: 82px;
  height: 82px;
  position: absolute;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid grey;
  box-shadow: 2px 2px 2px grey;
}
.kuai .youbian{
  margin-top: 10px;
  float: right;
  position: relative;
  width: 73%;
  height: 100px;
}
.kuai .youbian p{
  display: block;
  height: 22px;
  line-height: 22px;
  overflow: hidden;
  color: black;
}
.kuai .youbian .shuoming{
  font-size: 13px;
  color: #8f8f94;
}
.kuai .youbian .delete{
    position: absolute;
    width: 25px;
    height: 25px;
    right: 5%;
    top: 50%;
    transform: translateY(-50%);
}
.kuai .youbian .last{
  margin-top: 8px;
}
.kuai .youbian .last span{
  color: red;
  line-height: 20px;
}
.kuai .qiang{
  height: 10px;
  background-color: #f2f2f2;
}
</style>


