import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home/home.vue'
import goods01 from '@/pages/home/components/goods01.vue'
import classify from '@/pages/classify/classify.vue'
import shopcar from '@/pages/shopcar/shopcar.vue'
import user from '@/pages/user/user.vue'
import login from '@/pages/user/login.vue'
import reg from '@/pages/user/reg.vue'
import remima from '@/pages/user/remima.vue'
import xinxi from '@/pages/user/xinxi.vue'
import shoucang from '@/pages/user/shoucang.vue'
import dizhi from '@/pages/user/dizhi.vue'
import dizhiList from '@/pages/user/dizhiList.vue'
import goumai from '@/pages/shopcar/goumai.vue'
import dingdan from '@/pages/user/dingdan.vue'

Vue.use(Router)

export default new Router({
  routes: [
    //默认router-view会加载path为'/'的组件
    {
      path:"/",
      name:'home',
      component:home
    },
    {
      path:"/login",
      name:"login",
      component:login
    },
    {
      path:'/reg',
      name:'reg',
      component:reg
    },
    {
      path:"/home/goods01",
      name:"goods01",
      component:goods01
    },
    {
      path:'/classify',
      name:'classify',
      component:classify
    },
    {
      path:'/shopcar',
      name:'shopcar',
      component:shopcar
    },
    {
      path:'/user',
      name:'user',
      component:user
    },
    {
      path:'/user/remima',
      name:'remima',
      component:remima
    },
    {
      path:'/user/xinxi',
      name:'xinxi',
      component:xinxi
    },
    {
      path:'/user/shoucang',
      name:'shoucang',
      component:shoucang
    },
    {
      path:'/user/dizhi',
      name:'dizhi',
      component:dizhi
    },
    {
      path:'/user/dizhiList',
      name:'dizhiList',
      component:dizhiList
    },
    {
      path:'/goumai',
      name:'goumai',
      component:goumai
    },
    {
      path:'/dingdan',
      name:'dingdan',
      component:dingdan
    }
  ]
})



