<template>
  <div class="body">
			<div style="padding: 10px 10px;">
				<div id="segmentedControl" class="mui-segmented-control">
					<a class="mui-control-item mui-active" href="#item1">
				商品
			</a>
					<a class="mui-control-item" href="#item2">
				详情
			</a>
					<a class="mui-control-item" href="#item3">
				评论
			</a>
				</div>
			</div>
			<div>
				<div id="item1" class="mui-control-content mui-active">
                    <swiper   class="swiperImg" ref="mySwiper" :options="swiperOptions">
                    <swiper-slide v-for="(item,index) of JSON.parse(JSON.stringify(goodsdetail)).data.goodSwiperPhotoUrl" :key="index">
                        <img :src="item" alt="">
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
		            </swiper>
                    <div class="price">
                        <span class="one">￥</span>
                        <span class="two">{{JSON.parse(JSON.stringify(goodsdetail)).price}}</span>
                        <span class="three">{{JSON.parse(JSON.stringify(goodsdetail)).people}}</span>
                    </div>
                    <div class="text">
                        <span class="one">{{JSON.parse(JSON.stringify(goodsdetail)).text}}</span>
                    </div>
                    <div class="diqu">
                        <span class="one">{{JSON.parse(JSON.stringify(goodsdetail)).data.kuaidi}}</span>
                        <span class="two">{{JSON.parse(JSON.stringify(goodsdetail)).data.yuexiaoshou}}</span>
                        <span class="three">{{JSON.parse(JSON.stringify(goodsdetail)).data.fromPlace}}</span>
                    </div>
                    <div class="geban"></div>
                    <div class="fuwu">
                        <span class="one">服务</span>
                        <span class="two">{{JSON.parse(JSON.stringify(goodsdetail)).data.fuwu}}</span>
                    </div>
                    <div class="qiang"></div>
				</div>
				<div id="item2" class="mui-control-content">
                    <div class="detailImg">
					    <img v-for="(item2,index2) of JSON.parse(JSON.stringify(goodsdetail)).data.details" :key="index2" :src="item2" alt="">
                    </div>
                    <div class="qiang">
                    </div>
				</div>
				<div id="item3" class="mui-control-content">
					<div v-for="(item1,index1) of JSON.parse(JSON.stringify(goodsdetail)).data.pinlun" :key="index1" class="pinlun">
                        <div class="photo">
                            <img :src="item1.idImg" alt="">
                        </div>
                        <span>{{item1.id}}</span>
                        <p>{{item1.text}}</p>
                    </div>
                    <div class="qiang"></div>
				</div>
			</div>
            <div class="dibu">
                <div class="zuobian">
                    <router-link tag="div" to="/" class="shouye">
                        <img src="../../../../static/photo/shouye.png" alt="">
                        <p>首页</p>
                    </router-link>
                    <div @click="shoucang" class="shoucang">
                        <img src="../../../../static/photo/shoucang.png" alt="">
                        <p>收藏</p>
                    </div>
                    <router-link to="/shopcar" tag="div" class="gouwuce">
                        <img src="../../../../static/photo/gouwuce.png" alt="">
                        <p>购物车</p>
                    </router-link>
                </div>
                <div class="youbian">
                    <div @click="jiaru" class="jiaru">
                        <p>
                            加入购物车
                        </p>
                    </div>
                    <div class="goumai">
                        <p>
                            购买
                        </p>
                    </div>
                </div>
            </div>
  </div>
</template>

<script>
export default {
    name:'goods01',
    data(){
        return{
            swiperOptions: {
			loop:true,
          pagination: {
            el: '.swiper-pagination'
		  },
		  autoplay:{
			delay:3000,
			stopOnLastSlide: false,

    		/* 触摸滑动后是否继续轮播 */

    		disableOnInteraction: false
		  },
		  speed:1000,
          // Some Swiper option/callback...
        },
            goodsdetail:{data:{goodSwiperPhotoUrl:1}}
        }
    },
    methods:{
        jiaru(){
            let user=localStorage.getItem('user');
            if(user==undefined){
                alert("当前没有账号登录，请先登录账号！");
                return;
            }
            let id=this.goodsdetail.id;
            let name=this.goodsdetail.text;
            let time1=new Date()
            let time=time1.getFullYear()+'/'+(time1.getMonth()+1)+'/'+time1.getDate()+' '+time1.getHours()+':'+time1.getMinutes()+':'+time1.getSeconds();
            let photoUrl=this.goodsdetail.photoUrl;
            let price =this.goodsdetail.price;
            let kuaidi=this.goodsdetail.data.kuaidi;
            let fromPlace=this.goodsdetail.data.fromPlace;
            let shopcar1={
                'check':false,
                'id':id,
                'number':1,
                'name':name,
                'time':time,
                'photoUrl':photoUrl,
                'price':price,
                'kuaidi':kuaidi,
                'fromPlace':fromPlace,
            }
            
            let userData=JSON.parse(localStorage.getItem(user));
            // console.log(userData);
            if(userData.shopcar==undefined){
                userData.shopcar=[];
            }
            for(let i=0;i<userData.shopcar.length;i++){
                if(JSON.stringify(shopcar1.name)==JSON.stringify(userData.shopcar[i].name)){
                    userData.shopcar[i].number=userData.shopcar[i].number+1;
                    localStorage.setItem(user,JSON.stringify(userData));
                    alert("加入购物车成功！");
                    return;
                }
            }
            userData.shopcar.push(shopcar1);
            localStorage.setItem(user,JSON.stringify(userData));
            alert("加入购物车成功！");
        },
        shoucang(){
            let user=localStorage.getItem('user');
            if(user==undefined){
                alert("当前没有 账号登录，请先登录账号！");
                return;
            }
            let id=this.goodsdetail.id;
            let name=this.goodsdetail.text;
            let photoUrl=this.goodsdetail.photoUrl;
            let price=this.goodsdetail.price;
            let kuaidi=this.goodsdetail.data.kuaidi;
            let fromPlace=this.goodsdetail.data.fromPlace;
            let shoucang1={
                'id':id,
                'name':name,
                'photoUrl':photoUrl,
                'price':price,
                'kuaidi':kuaidi,
                'fromPlace':fromPlace
            }
            let userData=JSON.parse(localStorage.getItem(user));
            if(userData.shoucang==undefined){
                userData.shoucang=[];
            }
            for(let i=0;i<userData.shoucang.length;i++){
                if(JSON.stringify(shoucang1)==JSON.stringify(userData.shoucang[i])){
                    localStorage.setItem(user,JSON.stringify(userData));
                    alert("收藏成功");
                    return;
                }
            }
            userData.shoucang.push(shoucang1);
            localStorage.setItem(user,JSON.stringify(userData));
            alert("收藏成功");
        }
    },
    created(){
        var that=this;
        this.$http.post("http://www.dongxx.com/home/goods",{})
        .then(function(data){
            that.goodsdetail=data["data"]["data"];
            that.goodsdetail=eval("that.goodsdetail["+that.$route.query.id+"]");
            // console.log(that.goodsdetail);
        })
        .catch(function(err){
            console.log(err);
        })
    }
}
</script>

<style scoped>
.geban{
	background-color: rgb(244,244,244);
	width: 100%;
	height: 0.5rem;
}
.qiang{
    height: 52px;
}
.mui-segmented-control{
    border: 0;
}
.mui-segmented-control .mui-control-item{
    font-weight: bold;
    color: #4b566a;
    border: 0;
}
.mui-segmented-control .mui-control-item.mui-active{
    color: #e02e24;
    background-color: white;
    border-bottom: 2px solid #e02e24;
}
.mui-bar-tab{
    display: none;
}
.swiperImg img{
    width: 100%;
    height: 27rem;
}
.body{
    z-index: 998;
}
body{
    position: relative;
    border: 0;
    padding: 0;
    margin: 0;
}
body .dibu{
    z-index: 999;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 3rem;
    background-color: rgb(244,244,244);
}
.dibu .zuobian{
    display: inline-block;
    height: 100%;
    width: 49%;
}
.dibu .zuobian .shouye{
    margin: 0;
    padding: 0;
    border: 0;
    display: inline-block;
    height: 100%;
    width: 31%;
    position: relative;
}
.dibu .zuobian .shouye img{
    position: absolute;
    padding-top: 3px;
    width: 22px;
    left: 50%;
    transform: translate(-50%,0);
}
.dibu .zuobian .shouye p{
    position: absolute;
    font-size: 14px;
    height: 20px;
    left: 50%;
    bottom: 0;
    padding-top: 7px;
    transform: translate(-50%,0);
}
.dibu .zuobian .shoucang{
    margin: 0;
    padding: 0;
    border: 0;
    display: inline-block;
    height: 100%;
    width: 31%;
    position: relative;
}
.dibu .zuobian .shoucang img{
    position: absolute;
    padding-top: 3px;
    width: 22px;
    left: 50%;
    transform: translate(-50%,0);
}
.dibu .zuobian .shoucang p{
    position: absolute;
    font-size: 14px;
    height: 20px;
    left: 50%;
    bottom: 0;
    padding-top: 7px;
    transform: translate(-50%,0);
}
.dibu .zuobian .gouwuce{
    margin: 0;
    padding-left: 1px;
    border: 0;
    display: inline-block;
    height: 100%;
    width: 31%;
    position: relative;
}
.dibu .zuobian .gouwuce img{
    position: absolute;
    padding-top: 3px;
    width: 22px;
    left: 50%;
    transform: translate(-50%,0);
}
.dibu .zuobian .gouwuce p{
    position: absolute;
    font-size: 14px;
    height: 20px;
    bottom: 0;
    padding-top: 7px;
    padding-left: 9px;
}
.dibu .youbian{
    display: inline-block;
    height: 100%;
    width: 49%;
}
.dibu .youbian .jiaru{
    float: left;
    width: 50%;
    height: 100%;
    background-color: orange;
}
.dibu .youbian .jiaru p{
    line-height: 45px;
    color: white;
    text-align: center;
    font-size: 15px;
}
.dibu .youbian .goumai{
    overflow: hidden;
    height: 100%;
    background-color: orangered;
}
.dibu .youbian .goumai p{
    font-size: 15px;
    line-height: 45px;
    color: white;
    text-align: center;
}
.price{
    padding-left: 10px;
    margin-top: 13px;
}
.price .two{
    font-weight: bold;
    font-size: 33px;
    color: #e02e24;
}
.price .one{
    font-weight: bold;
    font-size: 16px;
    color: #e02e24;
}
.price .three{
    float: right;
    font-size: 10px;
    color: #58595b;
    margin-right: 14px;
}
.text{
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 20px;
    overflow: hidden;
}
.text .one{
    font-weight: bold;
    font-size: 16px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;

}
.diqu{
    padding: 10px;
    display: flex;
    justify-content: space-between;
    font-size: 11px;
    color: rgb(153, 153, 153);
}
.fuwu{
    overflow: hidden;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 40px;
    font-size: 15px;
    height: 30px;
}
.fuwu .one{
    color: rgb(153, 153, 153);
}
.fuwu .two{
    padding-left: 20px;
    
}
.pinlun{
    position: relative;
    padding: 10px;
}
.pinlun .photo{
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
}
.pinlun .photo img{
    border-radius: 50%;
    width: 100%;
    height: 100%;
}
.pinlun span{
    position: absolute;
    left: 40px;
    top: 10px;
    color: rgb(153, 153, 153);
    font-size: 14px;
}
.pinlun p{
    color: #58595b;
}
.detailImg img{
    width: 100%;
}
</style>


