<template>
  <div>
      <div class="dingbu">
        <span @click="onChangeRouter">&lt;</span>
        <p ref="title">添加地址</p>
    </div>
    <div class="text">
        <span>收货人</span>
        <input ref="shouhuo" type="text" placeholder="请输入收货人">
    </div>
    <div class="text">
        <span>手机号</span>
        <input ref="shouji" type="text" placeholder="请输入手机号">
    </div>
    <div class="diqu">
        <span>所在地区</span>
        <select name="" ref="province" @change="changeProvince" id="">
            <option value="">省级</option>
            <option v-for="(item,index) of province" :key="index" :selected="item.checked==true?true:false" value="">{{item.name}}</option>
        </select>
        <select ref="city" @change="changeCity"  name="" id="">
            <option  value="">地级</option>
            <option v-for="(item,index) of city" :key="index" value="">{{item.name}}</option>
        </select>
        <select ref="town" name="" id="">
            <option  value="">县级</option>
            <option v-for="(item,index) of town" :key="index" value="">{{item.name}}</option>
        </select>
    </div>
    <div class="text">
        <span>详细地址</span>
        <input ref="dizhi" type="text" placeholder="请输入详细地址">
    </div>
    <div class="moren">
        <span>设为默认地址</span>
        <input ref="moren" type="checkbox">
    </div>
    <button @click="baocun" class="baocun">
        保存此地址
    </button>
  </div>
</template>

<script>
export default {
    name:'dizhiList',
    data(){
        return{
            province:[],
            city:[],
            town:[],
            renameIndex:undefined
        }
    },
    methods:{
        onChangeRouter(){
            this.$router.replace('/user/dizhi');
        },
        changeProvince(){
            let index=this.$refs.province.selectedIndex;
            let that=this;
            if(index==0){
                this.city=[];
                this.town=[];
                this.$refs.city.options[0].selected=true;
                this.$refs.town.options[0].selected=true;
                return;
            }
            let number=this.province[index-1].id;
            // console.log(this.$refs.province.selectedIndex);
            this.$http.get("/diqu/area/city?parentid="+number+"&appkey=21f2fe03afb6b281")
            .then(function(data){
                that.city=data.data.result;
            }).catch(function(err){
                console.log(err);
            });
        },
        changeCity(){
            let index=this.$refs.city.selectedIndex;
            let that=this;
            if(index==0){
                this.town=[];
                this.$refs.town.options[0].selected=true;
                return;
            }
            // console.log(this.$refs.province.selectedIndex);
            let number=this.city[index-1].id;
            this.$http.get("/diqu/area/town?parentid="+number+"&appkey=21f2fe03afb6b281")
            .then(function(data){
                that.town=data.data.result;
            }).catch(function(err){
                console.log(err);
            });
        },
        baocun(){
            // console.log(this.$refs.moren.checked);
            if(this.$refs.shouhuo.value==""){
                alert("请输入收货人");
                return;
            }
            if(this.$refs.shouji.value==""){
                alert("请输入手机号");
                return;
            };
            if(this.$refs.province.options[this.$refs.province.selectedIndex].text=="省级"){
                alert("请选择省级地区");
                return;
            }
            if(this.$refs.city.options[this.$refs.city.selectedIndex].text=="地级"){
                alert("请选择地级地区");
                return;
            }
            if(this.$refs.dizhi.value==""){
                alert("请输入详细地址");
                return;
            }
            
        let userDizhi={
            'user':this.$refs.shouhuo.value,
            'phone':this.$refs.shouji.value,
            'province':this.$refs.province.options[this.$refs.province.selectedIndex].text,
            'city':this.$refs.city.options[this.$refs.city.selectedIndex].text,
            'town':this.$refs.town.options[this.$refs.town.selectedIndex].text,
            'xiangxi':this.$refs.dizhi.value,
            'checkedIf':this.$refs.moren.checked
        }
        console.log(userDizhi);
        let user=localStorage.getItem('user');
        let userData=JSON.parse(localStorage.getItem(user));
        if(userData.userDizhi==undefined){
            userData.userDizhi=[];
        }
        if(userDizhi.checkedIf==true){
            for(let i=0;i<userData.userDizhi.length;i++){
                if(userData.userDizhi[i].checkedIf==true){
                    userData.userDizhi[i].checkedIf=false;
                }
            }
        }
        if(this.$route.query.id!==undefined){
            userData.userDizhi[this.$route.query.id]=userDizhi;
        }else{
            userData.userDizhi.push(userDizhi);
        }
        localStorage.setItem(user,JSON.stringify(userData));
        alert("保存地址成功！");
        }
    },
    created(){
        this.renameIndex=this.$route.query.id;
        console.log(this.renameIndex);
        var that=this;
        this.$http.get("/diqu/area/province?appkey=21f2fe03afb6b281")
        .then(function(data){
            that.province=data.data.result;
            // console.log(that.province);
            if(that.renameIndex!==undefined){
                // console.log(gg);
            let user=localStorage.getItem('user');
            let userData=JSON.parse(localStorage.getItem(user));
            let dizhi=userData.userDizhi[that.renameIndex];
            // console.log(dizhi);
            // console.log(that.province)
            for(let i=0;i<that.province.length;i++){
                if(that.province[i].name==dizhi.province){
                    that.province[i].checked=true;
                }
            }
            console.log(that.province);
        }    
        }).catch(function(err){
            console.log(err);
        })
        
    },
    mounted(){
        if(this.renameIndex!==undefined){
            this.$refs.title.innerText="修改地址";
            let user=localStorage.getItem('user');
            let userData=JSON.parse(localStorage.getItem(user));
            let dizhi=userData.userDizhi[this.renameIndex];
            this.$refs.shouhuo.value=dizhi.user;
            this.$refs.shouji.value=dizhi.phone;
            this.$refs.dizhi.value=dizhi.xiangxi;
            this.$refs.moren.checked=dizhi.checkedIf;
            
        }
    }
}
</script>

<style scoped>
.dingbu{
    background-color: #f2f2f2;
    position: relative;
    height: 44px;
    width: 100%;
}
.dingbu span{
    line-height: 44px;
    font-size: 200%;
    margin-left: 10px;
    color: #8F8F94;
}
.dingbu p{
    line-height: 44px;
    font-size: 100%;
    display: inline-block;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.text{
    position: relative;
    height: 42px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    border-bottom: 1px solid #f2f2f2;
}
.text span{
    line-height: 20px;
    font-size: 16px;
}
.text input{
    position: absolute;
    width: 250px;
    height: 14px;
    line-height: 14px;
    font-size: 14px;
    top: 50%;
    transform: translateY(-50%);
    right: 10%;
    padding: 0;
    margin: 0;
    border: 0;
    
}
.diqu{
    position: relative;
    height: 42px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    border-bottom: 1px solid #f2f2f2;
}
.diqu span{
    line-height: 20px;
}
.diqu select:nth-child(2){
    position: absolute;
    float: left;
    width: 50px;
    height: 25px;
    padding: 0;
    top: 50%;
    transform: translateY(-50%);
    left: 25%;
    background: url('../../../static/userPhoto/select.png') no-repeat right center;
    background-size: 12px;
}
.diqu select:nth-child(3){
    position: absolute;
    float: left;
    width: 50px;
    height: 25px;
    padding: 0;
    margin: 0;
    top: 50%;
    transform: translateY(-50%);
    left: 40%;
    background: url('../../../static/userPhoto/select.png') no-repeat right center;
    background-size: 12px;
}
.diqu select:nth-child(4){
    position: absolute;
    float: left;
    width: 50px;
    height: 25px;
    padding: 0;
    margin: 0;
    top: 50%;
    transform: translateY(-50%);
    left: 55%;
    background: url('../../../static/userPhoto/select.png') no-repeat right center;
    background-size: 12px;
}
.moren{
    position: relative;
    height: 42px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    border-bottom: 1px solid #f2f2f2;
}
.moren span{
    line-height: 20px;
}
.moren input{
    position: absolute;
    width: 20px;
    height: 20px;
    top: 50%;
    transform: translate(-50%,-50%);
    left: 50%;
    padding: 0;
    margin: 0;
    border: 0;
}
.baocun{
    margin-top: 20px;
    position: relative;
    width: 90%;
    height: 40px;
    background-color: green;
    color: white;
    border-radius: 5px;
    left: 50%;
    transform: translateX(-50%);
}
</style>


