<template>
  <div class="box">
      <div @click="goHomeRouter" class="fanhui">
          <img src="../../../static/photo/fanhui.png" alt="">
      </div>
      <div class="denglu_huanying">
          欢迎登录商城
      </div>
      <div class="yonghuming">
          <img src="../../../static/photo/yonghuming.png" alt="">
          <input type="text" ref="userName" placeholder="请输入用户名">
      </div>
      <div class="mima">
          <img src="../../../static/photo/mima.png" alt="">
          <input type="password" ref="userMima" placeholder="请输入密码">
      </div>
      <button @click="denglu" class="denglu">立即登录</button>
      <div class="dibu">
          <span class="one" @click="bangzhu">系统帮助</span>
          <span class="two" @click="changeRouter">免费注册</span>
      </div>
  </div>
</template>

<script>
export default {
    name: 'login',
    methods:{
        goHomeRouter(){
            this.$router.replace('/');
        },
        changeRouter(){
            this.$router.replace('/reg');
        },
        bangzhu(){
            alert("就不帮你!^_^");
        },
        denglu(){
            if(this.$refs.userName.value==""){
                alert("请输入用户名");
                return;
            }
            if(this.$refs.userMima.value==""){
                alert("请输入密码");
                return;
            }
            let name=JSON.parse(localStorage.getItem(this.$refs.userName.value)).userName;
            let mima=JSON.parse(localStorage.getItem(this.$refs.userName.value)).userMima;
            // console.log(JSON.parse(localStorage.getItem(this.$refs.userName.value)));
            if(this.$refs.userName.value==name&this.$refs.userMima.value==mima){
                localStorage.setItem("user",name);
                localStorage.getItem("user");
                this.$router.replace({path:'/user',query:{id:name}});
                return;
            }else{
                alert("登录失败，用户名或者密码错误");
                return;
            }
            // this.$http.post("http://www.dongxx.com/user",{})
            // .then(function(data){
            //     console.log(data);
            // })
            // .catch(function(err){
            //     console.log(err);
            // })
        }
    },
    
}
</script>

<style scoped>
.box{
    height: 50rem;
    position: relative;
    background-color: #5b5b5b;
    z-index: 999;

}
.box .denglu_huanying{
    position: absolute;
    left: 50%;
    top: 10%;
    transform: translate(-50%,0);
    color: white;
    font-size: 18px;
}
.box .yonghuming{
    position: absolute;
    width: 90%;
    left: 50%;
    top: 20%;
    transform: translate(-50%,0);
    background-color: #a8a8a8 !important;
    border-radius: 5px;
    height: 37px;
}
.box .yonghuming img{
    display: inline-block;
    height: 20px;
    margin-top: 8px;
    margin-left: 8px;
}
.box .yonghuming input{
    position: absolute;
    top: 50%;
    left: 12%;
    transform: translate(0,-50%);
    display: inline-block;
    width: 80%;
    height: 20px;
    background-color: #a8a8a8;
    border: 0;
    color: white;
}
.box .mima{
    position: absolute;
    width: 90%;
    left: 50%;
    top: 30%;
    transform: translate(-50%,0);
    background-color: #a8a8a8 !important;
    border-radius: 5px;
    height: 37px;
}
.box .mima img{
    display: inline-block;
    height: 20px;
    margin-top: 8px;
    margin-left: 8px;
}
.box .mima input{
    position: absolute;
    top: 50%;
    left: 12%;
    transform: translate(0,-50%);
    display: inline-block;
    width: 80%;
    height: 20px;
    background-color: #a8a8a8;
    border: 0;
    color: white;
}
.box .denglu{
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    top: 40%;
    height: 37px;
    width: 80%;
    background-color: green;
    color: white;
}
.box .dibu{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,0);
    width: 90%;
}
.box .dibu .one{
    float: left;
    color: white;
}
.box .dibu .two{
    float: right;
    color: white;
}
.box .fanhui{
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    top: 60%;
    width: 5rem;
}
.box .fanhui img{
    width: 100%;
}
</style>


