<template>
  <div class="box">
    <div class="dingbu">
      <p>购物车</p>
    </div>
    <div @click="returnGood(item)" v-for="(item,index) of shopcarData" :key="index" class="kuai">
      <div class='time'>
        <p>时间：{{item.time}}</p>
        <img @click.stop="deleteAll(index)" src="../../../static/userPhoto/delete.png" alt="">
      </div>
      <div class="zuobian">
        <input @click.stop="checkBox(index)" ref="checkBox" :checked="item.check" type="checkbox">
          <img class="goodPhoto" :src="item.photoUrl" alt="">  
      </div>
      <div class="youbian">
        <p>{{item.name}}</p>
        <span class="shuoming">{{item.kuaidi}} | {{item.fromPlace}}</span>
        <div class="last">
          <span>￥{{item.price}}</span>
          <img class="jia" @click.stop="jiaNumber(index,item)" src="../../../static/userPhoto/jia.png" alt="">
          <input type="text" :value="item.number">
          <img class="jian" @click.stop="jianNumber(index,item)" src="../../../static/userPhoto/jian.png" alt="">
        </div>
      </div>
      <div class="qiang"></div>
    </div>
    <div class="dibu">
        <input type="checkbox" ref="checkAll" @click="checkAll" :checked="checkIf">
        <span class="quanxuan">全选</span>
        <div class="jiesuan">
          <button @click="goumai">结算</button>
        </div>
        <p>合计:<span>￥{{price}}</span></p>
    </div>
    <homeFooter></homeFooter>
  </div>
</template>

<script>
import homeFooter from '../home/components/homeFooter.vue'
export default {
    name:'shopcar',
    data(){
      return {
        shopcarData:[],
        checkIf:false,
        price:0
      }
    },
    components:{
      homeFooter
    },
    methods:{
      returnGood(item){
        this.$router.replace({path:'/home/goods01',query:{id:item.id-1}});
      },
      deleteAll(index){
            this.price=0;
            var a=confirm("你真的要删除该商品吗？");
            if(a=false){
                return;
            }
            let user=localStorage.getItem('user');
            let userData=JSON.parse(localStorage.getItem(user));
            let shopcarData=userData.shopcar;
            for(let i=index;i<shopcarData.length-1;i++){
                shopcarData[i]=shopcarData[i+1];
            }
            shopcarData.pop();
            userData.shopcar=shopcarData;
            this.shopcarData=shopcarData;
            for(let i=0;i<userData.shopcar.length;i++){
              if(userData.shopcar[i].check==true){
                this.price=this.price+(userData.shopcar[i].price)*(userData.shopcar[i].number);
              }
            }
            let goodNumber=userData.shopcar.length;
            //vuex 
            this.$store.commit('changeNumber',goodNumber);
            localStorage.setItem(user,JSON.stringify(userData));
        },
    jiaNumber(index,item){
      this.price=0;
      let user=localStorage.getItem('user');
      // console.log(user);
      let userData=JSON.parse(localStorage.getItem(user));
      userData.shopcar[index].number=item.number+1;
      this.shopcarData=userData.shopcar;
      for(let i=0;i<userData.shopcar.length;i++){
        if(userData.shopcar[i].check==true){
          this.price=this.price+(userData.shopcar[i].price)*(userData.shopcar[i].number);
        }
      }
      localStorage.setItem(user,JSON.stringify(userData));
    },
    jianNumber(index,item){
      this.price=0
      if(item.number==1){
        return;
      }
      let user=localStorage.getItem('user');
      let userData=JSON.parse(localStorage.getItem(user));
      userData.shopcar[index].number=item.number-1;
      this.shopcarData=userData.shopcar;
      for(let i=0;i<userData.shopcar.length;i++){
        if(userData.shopcar[i].check==true){
          this.price=this.price+(userData.shopcar[i].price)*(userData.shopcar[i].number);
        }
      }
      localStorage.setItem(user,JSON.stringify(userData));
    },
    checkBox(index){
      this.price=0;
      let checkIf=this.$refs.checkBox[index].checked;
      // console.log(checkIf);
      let user=localStorage.getItem('user');
      let userData=JSON.parse(localStorage.getItem(user));
      userData.shopcar[index].check=checkIf;
      // console.log(userData.shopcar[index].check);
      this.shopcarData=userData.shopcar;
      this.checkIf=true;
      for(let i=0;i<userData.shopcar.length;i++){
        if(userData.shopcar[i].check==false){
          this.checkIf=false;
        }
        if(userData.shopcar[i].check==true){
          this.price=this.price+(userData.shopcar[i].price)*(userData.shopcar[i].number);
        }
      }
      localStorage.setItem(user,JSON.stringify(userData));
    },
    checkAll(){
      this.price=0;
      // console.log(this.$refs.checkAll);
      let checkIf=this.$refs.checkAll.checked;
      // console.log(this.$refs.checkAll);
      let user=localStorage.getItem('user');
      let userData=JSON.parse(localStorage.getItem(user));
      for(let i=0;i<userData.shopcar.length;i++){
        userData.shopcar[i].check=checkIf;
        if(userData.shopcar[i].check==true){
          this.price=this.price+(userData.shopcar[i].price)*(userData.shopcar[i].number);
        }
      }
      this.shopcarData=userData.shopcar;
      localStorage.setItem(user,JSON.stringify(userData));
      this.checkIf=checkIf;
    },
    goumai(){
      let checkedIf=false;
      let user=localStorage.getItem('user');
      let userData=JSON.parse(localStorage.getItem(user));
      if(userData.shopcar.length==0||userData.shopcar==undefined){
        alert("请加购商品到购物车中");
        return;
      }
      for(let i=0;i<userData.shopcar.length;i++){
        if(userData.shopcar[i].check==true){
          checkedIf=true;
        }
      }
      if(checkedIf==false){
        alert("请勾选要购买的商品");
        return;
      }
      this.$router.replace("/goumai");
    }
    },
    created(){
      let user=localStorage.getItem('user');
      let userData=JSON.parse(localStorage.getItem(user));
      this.shopcarData=userData.shopcar;
      this.checkIf=true;
      if(userData.shopcar.length==0||userData.shopcar==undefined){
        this.checkIf=false;
      }
      for(let i=0;i<userData.shopcar.length;i++){
        if(userData.shopcar[i].check==true){
          this.price=this.price+(userData.shopcar[i].price)*(userData.shopcar[i].number);
        }
        if(userData.shopcar[i].check==false){
          this.checkIf=false;
        }
      }
    }
}
</script>
<style scoped>
  .dingbu{
    width: 100%;
    height: 44px;
    background-color: #f2f2f2;
  }
  .dingbu p{
    text-align: center;
    line-height: 44px;
    margin-bottom: 0px;
    color: dimgray;
    font-size: 100%;
  }
  .dibu{
    position: fixed;
    width: 100%;
    height: 56px;
    bottom: 7%;
    border-top: 1px solid gray;
  }
  .dibu .jiesuan{
    margin: 8px 10px 12px 10px;
    float: right;
    height: 36px;
    width: 100px;
  }

  .dibu .jiesuan button{
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background-color: orangered;
    color: white;
  }
  .dibu  input{
    position: absolute;
    top: 50%;
    left: 5%;
    transform: translateY(-50%);
  }
  .dibu  .quanxuan{
    position: absolute;
    top: 50%;
    left: 10%;
    transform: translateY(-50%);
  }
  .dibu p{
    float: right;
    margin-top: 20px;
    font-size: 110%;
    color: black;
  }
  .dibu p span{
    color: orangered;
    font-size: 90%;
  }
.kuai .time{
  width: 100%;
  height: 40px;
  padding: 10px 5px;
}
.kuai .time p{
  display: inline-block;
  width: 80%;
}
.kuai .time img{
  float: right;
  height: 18px;
  width: 18px;
  margin-right: 10px;
}
.kuai .zuobian{
  display: inline-block;
  position: relative;
  width: 35%;
  height: 100px;
}
.kuai .zuobian input{
  position: absolute;
  width: 20px;
  height: 20px;
  left: 5%;
  top: 50%;
  transform: translateY(-50%);
}

.kuai .zuobian .goodPhoto{
  display: inline-block;
  width: 82px;
  height: 82px;
  position: absolute;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid grey;
  box-shadow: 2px 2px 2px grey;
}
.kuai .youbian{
  margin-top: 10px;
  float: right;
  position: relative;
  width: 63%;
  height: 100px;
}
.kuai .youbian p{
  display: block;
  height: 22px;
  line-height: 22px;
  overflow: hidden;
  color: black;
}
.kuai .youbian .shuoming{
  font-size: 13px;
  color: #8f8f94;
}
.kuai .youbian .last{
  margin-top: 8px;
}
.kuai .youbian .last span{
  color: red;
  line-height: 20px;
}
.kuai .youbian .last .jian{
  float: right;
  width: 22px;
  height: 22px;
}
.kuai .youbian .last .jia{
  float: right;
  width: 22px;
  height: 22px;
  margin-right: 8px;
}
.kuai .youbian input{
  line-height: 13px;
  text-align: center;
  font-size: 13px;
  padding: 0;
  margin: 0;
  margin-top: 3px;
  width: 25px;
  height: 16px;
  float: right;
}
.kuai .qiang{
  height: 10px;
  background-color: #f2f2f2;
}
</style>


