<template>
  <div>
      <div class="mui-content mui-row mui-fullscreen">
			<div class="mui-col-xs-3">
				<div id="segmentedControls" class="mui-segmented-control mui-segmented-control-inverted mui-segmented-control-vertical">
          <a class="mui-control-item mui-active" href="#content1">分类1</a>
          <a class="mui-control-item" href="#content2">分类2</a>
					<a class="mui-control-item" href="#content3">分类3</a>
				</div>
			</div>
			<div id="segmentedControlContents" class="mui-col-xs-9" style="border-left: 1px solid #c8c7cc;">
        <div id="content1" class="mui-control-content mui-active">
          <router-link tag="div" :to="{path:'/home/goods01',query:{id:0}}" class="goods"  >  
            <div class="picture">
              <img :src="JSON.parse(JSON.stringify(goodsDetail))[0].photoUrl" alt="">
            </div>
            <div class="text">
              <div class="goodDesc">
                {{JSON.parse(JSON.stringify(goodsDetail))[0].text}}
              </div>
              <div class="goodPrice">
                <p class="p1">￥{{JSON.parse(JSON.stringify(goodsDetail))[0].price}}</p>
                <p class="p2">{{JSON.parse(JSON.stringify(goodsDetail))[0].people}}</p>
              </div>
              <div class="goodBottom">
                <div class="yunfei">{{JSON.parse(JSON.stringify(goodsDetail))[0].data.kuaidi}}</div>
                <div class="diqu">{{JSON.parse(JSON.stringify(goodsDetail))[0].data.fromPlace}}</div>
              </div>
            </div>
          </router-link>
        </div>
        <div id="content2" class="mui-control-content">
          <router-link tag="div" :to="{path:'/home/goods01',query:{id:1}}" class="goods"  >  
            <div class="picture">
              <img :src="JSON.parse(JSON.stringify(goodsDetail))[1].photoUrl" alt="">
            </div>
            <div class="text">
              <div class="goodDesc">
                {{JSON.parse(JSON.stringify(goodsDetail))[1].text}}
              </div>
              <div class="goodPrice">
                <p class="p1">￥{{JSON.parse(JSON.stringify(goodsDetail))[1].price}}</p>
                <p class="p2">{{JSON.parse(JSON.stringify(goodsDetail))[1].people}}</p>
              </div>
              <div class="goodBottom">
                <div class="yunfei">{{JSON.parse(JSON.stringify(goodsDetail))[1].data.kuaidi}}</div>
                <div class="diqu">{{JSON.parse(JSON.stringify(goodsDetail))[1].data.fromPlace}}</div>
              </div>
            </div>
          </router-link>
        </div>
        <div id="content3" class="mui-control-content">
          <router-link tag="div" :to="{path:'/home/goods01',query:{id:2}}" class="goods"  >  
            <div class="picture">
              <img :src="JSON.parse(JSON.stringify(goodsDetail))[2].photoUrl" alt="">
            </div>
            <div class="text">
              <div class="goodDesc">
                {{JSON.parse(JSON.stringify(goodsDetail))[2].text}}
              </div>
              <div class="goodPrice">
                <p class="p1">￥{{JSON.parse(JSON.stringify(goodsDetail))[2].price}}</p>
                <p class="p2">{{JSON.parse(JSON.stringify(goodsDetail))[2].people}}</p>
              </div>
              <div class="goodBottom">
                <div class="yunfei">{{JSON.parse(JSON.stringify(goodsDetail))[2].data.kuaidi}}</div>
                <div class="diqu">{{JSON.parse(JSON.stringify(goodsDetail))[2].data.fromPlace}}</div>
              </div>
            </div>
          </router-link>
        </div>
				
			</div>
		</div>
    <homeFooter></homeFooter>
  </div>
</template>

<script>
import homeFooter from '../home/components/homeFooter.vue'
import mui from '../../mui/js/mui.js'
mui.init({
  swipeBack: true
});
// 默认选中第一个


export default {
    name:'classify',
    data(){
      return{
        goodsDetail:[{photoUrl:0,data:{kuaidi:0}},{photoUrl:1,data:{kuaidi:1}},{photoUrl:2,data:{kuaidi:2}}],
      }
    },
    components:{
      homeFooter
    },
    created(){
      var that=this;
      this.$http.post("http://www.dongxx.com/home/goods",{})
      .then(function(data){
        that.goodsDetail=data["data"]["data"];
      })
      .catch(function(err){
        console.log(err);
      })
    }

}
</script>

<style scoped>
.mui-row.mui-fullscreen>[class*="mui-col-"] {
  height: 100%;
}
.mui-col-xs-3,
.mui-control-content {
  overflow-y: auto;
  height: 100%;
}
.mui-segmented-control .mui-control-item {
  line-height: 50px;
  width: 100%;
}
.mui-segmented-control.mui-segmented-control-inverted .mui-control-item.mui-active {
  background-color: #fff;
}
.goods{
	position: relative;
	width: 17rem;
	height: 8rem;
	margin-top: 0.5rem;
	margin-left: 0.2rem;
	margin-right: 0.2rem;
}
.goods .picture img{
	width: 8rem;
	height: 8rem;
	
}
.goods .goodDesc{
	position: absolute;
	left: 48%;
	top: 0;
	width: 9rem;
	height: 3rem;
	overflow: hidden;
	line-height: 1.5rem;
	font-size: 0.9rem;
}
.goods .goodPrice{
	position: absolute;
	left: 50%;
	top: 60%;
	height: 1.5rem;
	
}
.goods .goodPrice .p1{
	display: inline-block;
	color: red;
	font-size: 1.4rem;
}
.goods .goodPrice .p2{
	display: inline-block;
	font-size: 0.6rem;
}
.goods .goodBottom{
	position: absolute;
	left: 50%;
	top: 85%;
	width: 8.5rem;
	height: 0.4rem;
}
.goods .goodBottom .yunfei{
	float: left;
	font-size: 0.5rem;
}
.goods .goodBottom .diqu{
	float: right;
	font-size: 0.5rem;
}
</style>


