<template>
  <div>
      <div class="dingbu">
          <span @click="onChangeRouter" >&lt;</span>
          <p>会员订单</p>
      </div>
      <div style="padding: 10px 10px 0px 10px;">
				<div id="segmentedControl" class="mui-segmented-control">
					<a ref="fukuan" class="mui-control-item mui-active" href="#item1">
                        待付款
			        </a>
					<a ref="fahuo" class="mui-control-item" href="#item2">
				        待发货
			        </a>
					<a ref="shouhuo" class="mui-control-item" href="#item3">
				        待收货
			        </a>
                    <a ref="pingjia" class="mui-control-item" href="#item4">
                        待评论
                    </a>
                    <a ref="quanbu" @click="quanbu" class="mui-control-item" href="#item5">
                        全部
                    </a>
				</div>
			</div>
			<div>
				<div ref="fukuanDiv" id="item1" class="mui-control-content mui-active">
                    <div v-for="(item,index) of JSON.parse(JSON.stringify(fukuan))" :key="index" class="fukuan">
                        <div class="qiang"></div>
                        <div>
                            <div class="danhao">
                                <span>单号：{{item.id}}</span>
                                <span>待付款</span>
                            </div>
                            <router-link  v-for="(item1,index1) of item.data"  :to="{path:'/home/goods01',query:{id:item1.id-1}}" tag="div" :key="index1" class="kuai">
                                <div class="zuobian">
                                    <img class="goodPhoto" :src="item1.photoUrl" alt="">  
                                </div>
                                <div class="youbian">
                                    <p>{{item1.name}}</p>
                                    <span class="shuoming">{{item1.kuaidi}} | {{item1.fromPlace}}</span>
                                    <div class="last">
                                    <span>￥{{item1.price}}</span>
                                    <span>&times;{{item1.number}}</span>
                                    </div>
                                </div>
                                <div class="xian"></div>
                            </router-link>
                            <div class="zongjie">
                                <p>{{item.data.length}}件商品 | 总金额：<span>￥{{priceAll(item.data)}}</span></p>
                            </div>
                            <div class="dibu">
                                    <button @click="quxiao(item)">取消订单</button>
                                    <button @click="qufukuan(item,index)">去付款</button>
                            </div>
                        </div>
                    </div>
                    <div style="height:100px"></div>
				</div>
				<div id="item2" ref="fahuoDiv" class="mui-control-content">
                    <div v-for="(item,index) of JSON.parse(JSON.stringify(shouhuo))" :key="index" class="fukuan">
                        <div class="qiang"></div>
                        <div>
                            <div class="danhao">
                                <span>单号：{{item.id}}</span>
                                <span>待发货</span>
                            </div>
                            <router-link  v-for="(item1,index1) of item.data"  :to="{path:'/home/goods01',query:{id:item1.id-1}}" tag="div" :key="index1" class="kuai">
                                <div class="zuobian">
                                    <img class="goodPhoto" :src="item1.photoUrl" alt="">  
                                </div>
                                <div class="youbian">
                                    <p>{{item1.name}}</p>
                                    <span class="shuoming">{{item1.kuaidi}} | {{item1.fromPlace}}</span>
                                    <div class="last">
                                    <span>￥{{item1.price}}</span>
                                    <span>&times;{{item1.number}}</span>
                                    </div>
                                </div>
                                <div class="xian"></div>
                            </router-link>
                            <div class="zongjie">
                                <p>{{item.data.length}}件商品 | 总金额：<span>￥{{priceAll(item.data)}}</span></p>
                            </div>
                        </div>
                    </div>
                    <div style="height:100px"></div>
				</div>
				<div id="item3" ref="shouhuoDiv" class="mui-control-content">
					3
				</div>
                <div id="item4" ref="pingjiaDiv" class="mui-control-content">
					4
				</div>
                <div id="item5" ref="quanbuDiv" class="mui-control-content">
					<div v-for="(item,index) of JSON.parse(JSON.stringify(quanbu))" :key="index" class="fukuan">
                        <div class="qiang"></div>
                        <div>
                            <div class="danhao">
                                <span>单号：{{item.id}}</span>
                                <span>{{item.quxiao==true?waitQuxiao:item.goumaiIf==true?waitFahuo:waitFukuan}}</span>
                            </div>
                            <router-link  v-for="(item1,index1) of item.data"  :to="{path:'/home/goods01',query:{id:item1.id-1}}" tag="div" :key="index1" class="kuai">
                                <div class="zuobian">
                                    <img class="goodPhoto" :src="item1.photoUrl" alt="">  
                                </div>
                                <div class="youbian">
                                    <p>{{item1.name}}</p>
                                    <span class="shuoming">{{item1.kuaidi}} | {{item1.fromPlace}}</span>
                                    <div class="last">
                                    <span>￥{{item1.price}}</span>
                                    <span>&times;{{item1.number}}</span>
                                    </div>
                                </div>
                                <div class="xian"></div>
                            </router-link>
                            <div class="zongjie">
                                <p>{{item.data.length}}件商品 | 总金额：<span>￥{{priceAll(item.data)}}</span></p>
                            </div>
                            <div class="dibu" v-if="!item.goumaiIf">
                                    <button @click="quxiao(item)">取消订单</button>
                                    <button @click="qufukuan(item,index)">去付款</button>
                            </div>
                        </div>
                    </div>
                    <div style="height:100px"></div>
				</div>
			</div>
  </div>
</template>

<script>
export default {
    name:'dingdan',
    data(){
        return{
            waitQuxiao:"已取消",
            waitFahuo:"待发货",
            waitFukuan:"待付款",
            fukuan:[],
            shouhuo:[],
            quanbu:[]
        }
    },
    computed:{
        // priceAll(data){
        //     let price=0;
        //     for(let i=0;i<data.lenth;i++){
        //         price=price+data[i].number*data[i].price;
        //     }
        //     return price;
        // }
    },
    methods:{
        onChangeRouter(){
            if(this.$route.query.id==undefined){
                this.$router.replace('/shopcar');
            }else{
                this.$router.replace('/user');
            }
        },
        priceAll(data){
            let price=0;
            for(let i=0;i<data.length;i++){
                price=price+data[i].number*data[i].price;
            }
            return price;
        },
        quxiao(item){
            let ifQuxiao=confirm("你确定要取消该订单吗？")
            if(ifQuxiao==false){
                return;
            }
            let user=localStorage.getItem('user');
            let userData=JSON.parse(localStorage.getItem(user));
            for(let j=0;j<userData.userFukuan.length;j++){
                if(userData.userFukuan[j].id==item.id){
                    for(let i=j;i<userData.userFukuan.length-1;i++){
                        userData.userFukuan[i]=userData.userFukuan[i+1];
            }
                }
            }
            userData.userFukuan.pop();
            // console.log(item);
            for(let j=0;j<userData.userAll.length;j++){
                if(userData.userAll[j].id==item.id){
                    // console.log(item);
                    for(let i=j;i<userData.userAll.length-1;i++){
                        userData.userAll[i]=userData.userAll[i+1];
            }
                }
            }
            userData.userAll.pop();
            item.goumaiIf=true;
            item.quxiao=true;
            userData.userAll.unshift(item);
            this.fukuan=userData.userFukuan;
            this.quanbu=userData.userAll;
            // console.log(userData.userFukuan,userData.userAll)
            localStorage.setItem(user,JSON.stringify(userData));
        },
        qufukuan(item,index){
            let user=localStorage.getItem('user');
            let userData=JSON.parse(localStorage.getItem(user));
            if(userData.userShouhuo==undefined){
                userData.userShouhuo=[]
            }
            // console.log(index,userData.userFukuan.length);
            userData.userShouhuo.push(item);
            for(let i=index;i<userData.userFukuan.length-1;i++){
                userData.userFukuan[i]=userData.userFukuan[i+1];
            }
            userData.userFukuan.pop();
            // console.log(userData.userFukuan);
            for(let j=0;j<userData.userAll.length;j++){
                if(userData.userAll[j].id==item.id){
                    let a=userData.userAll[j];
                    console.log(j);
                    a.goumaiIf=true;
                    for(let x=j;x<userData.userAll.length-1;x++){
                        // console.log(userData.userAll[x]);
                        userData.userAll[x]=userData.userAll[x+1];
                    }
                    // console.log(userData.userAll)
                    userData.userAll.pop();
                    userData.userAll.unshift(a);
                    // console.log(userData.userAll)
                    break;
                }
            }
            localStorage.setItem(user,JSON.stringify(userData));
            this.fukuan=userData.userFukuan;
            this.shouhuo=userData.userShouhuo;
            this.quanbu=userData.userAll;
            // console.log(userData.userAll);
            // console.log(userData.userFukuan);
            // console.log(userData.userShouhuo);
        }
    },
    mounted(){
        let user=localStorage.getItem('user');
        let userData=JSON.parse(localStorage.getItem(user));
        this.fukuan=userData.userFukuan;
        this.shouhuo=userData.userShouhuo;
        this.quanbu=userData.userAll;
        let zhuangtai=this.$route.query.id;
        // console.log(zhuangtai);
        this.$refs.quanbu.classList.remove('mui-active');
        this.$refs.fukuan.classList.remove('mui-active');
        this.$refs.fahuo.classList.remove('mui-active');
        this.$refs.shouhuo.classList.remove('mui-active');
        this.$refs.pingjia.classList.remove('mui-active');
        this.$refs.quanbuDiv.classList.remove('mui-active');
        this.$refs.fukuanDiv.classList.remove('mui-active');
        this.$refs.fahuoDiv.classList.remove('mui-active');
        this.$refs.shouhuoDiv.classList.remove('mui-active');
        this.$refs.pingjiaDiv.classList.remove('mui-active');
        if(zhuangtai=="quanbu"){
            this.$refs.quanbu.classList.add('mui-active');
            this.$refs.quanbuDiv.classList.add('mui-active');
        }
        if(zhuangtai=="fukuan"||zhuangtai==undefined){
            this.$refs.fukuan.classList.add('mui-active');
            this.$refs.fukuanDiv.classList.add('mui-active');
        }
        if(zhuangtai=="fahuo"){
            this.$refs.fahuo.classList.add('mui-active');
            this.$refs.fahuoDiv.classList.add('mui-active');
        }
        if(zhuangtai=="shouhuo"){
            this.$refs.shouhuo.classList.add('mui-active');
            this.$refs.shouhuoDiv.classList.add('mui-active');
        }
        if(zhuangtai=="pingjia"){
            this.$refs.pingjia.classList.add('mui-active');
            this.$refs.pingjiaDiv.classList.add('mui-active');
        }
        
    }
}
</script>

<style scoped>
.dingbu{
    background-color: #f2f2f2;
    position: relative;
    height: 44px;
    width: 100%;
}
.dingbu span{
    line-height: 44px;
    font-size: 200%;
    margin-left: 10px;
    color: #8F8F94;
}
.dingbu p{
    line-height: 44px;
    font-size: 100%;
    display: inline-block;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.mui-segmented-control{
    border: 0;
}
.mui-segmented-control .mui-control-item{
    border: 0;
    color: black;
}
.mui-segmented-control .mui-control-item.mui-active{
    background-color: white;
    border-bottom: 2px solid red;
    color: red;
    font-weight: bold;
}
.qiang{
  height: 10px;
  background-color: #f2f2f2;
}
.danhao{
    height: 42px;
    padding: 10px 10px 0px 10px;
    border-bottom: 1px solid #f3f3f3;
}
.danhao span{
    font-size: 15px;
}
.danhao span:nth-child(2){
    float: right;
}
.kuai .zuobian{
  display: inline-block;
  position: relative;
  width: 25%;
  height: 100px;
}

.kuai .zuobian .goodPhoto{
  display: inline-block;
  width: 82px;
  height: 82px;
  position: absolute;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid grey;
  box-shadow: 2px 2px 2px grey;
}
.kuai .youbian{
  margin-top: 10px;
  float: right;
  display: inline-block;
  position: relative;
  width: 73%;
}
.kuai .youbian p{
  display: block;
  height: 22px;
  line-height: 22px;
  overflow: hidden;
  color: black;
}
.kuai .youbian .shuoming{
  font-size: 13px;
  color: #8f8f94;
}
.kuai .youbian .last{
  margin-top: 8px;
}
.kuai .youbian .last span:nth-child(1){
  color: red;
  line-height: 20px;
}
.kuai .youbian .last span:nth-child(2){
  float: right;
  margin-right: 10px;
}
.kuai .xian{
  height: 1px;
  background-color: #f2f2f2;
}
.zongjie{
    height: 32px;
}
.zongjie p{
    line-height: 32px;
    display: inline-block;
    font-size: 15px;
    color: dimgray;
    float: right;
    margin-bottom: 0;
    margin-right: 10px;
}
.zongjie p span{
    color: red;
}
.dibu{
    position: relative;
    height: 43px;
    background-color: #f2f2f2;
}
.dibu button:nth-child(1){
    position: absolute;
    top: 50%;
    right: 20%;
    transform: translateY(-50%);
    border: 1px solid grey;
    border-radius: 5px;
    color: dimgray;
    margin: 5px;
    padding: 5px;
}
.dibu button:nth-child(2){
    position: absolute;
    top: 50%;
    right: 3%;
    transform: translateY(-50%);
    border: 1px solid grey;
    border-radius: 5px;
    color: dimgray;
    margin: 5px;
    padding: 5px;
}
</style>


